.searchPage_logo{
    object-fit: contain;
    height: 50px;
    margin-right: 50px;
}

.searchPage_header{
    display: flex;
    position: sticky;
    top: 0;
    background-color: white;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
    z-index: 100;
}

/* we can overide the css */
.searchPage_headerBody > .search > .search_input{

    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
}

.searchPage_options{
    display: flex;
    align-items: center;
    color: grey;
}
 
.searchPage_options a, .searchPage_optionsRight a{
    text-decoration: none;
    color: grey;
} 

.searchPage_optionsRight,.searchPage_optionsLeft{
    display: flex;
}
.searchPage_optionsRight{
    margin-left: 80px;
}

.searchPage_option{
    display: flex;
    align-items: center;
    margin-right: 20px;
}

.searchPage_option a{
    margin-left: 5px;
}

.searchPage_result{
    margin: 40px 0;
}

.searchPage_resultCount{
    color: grey;
    font-size: 14px;
}

.searchPage_results{
    max-width:650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;
}

.searchPage_resultTitle{
    text-decoration: none;
}

.searchPage_resultTitle{
    text-decoration: underline;
}

.searchPage_resultTitle > h2{
    font-weight: 500;
}

.searchPage_resultSnippet{
    margin-top: 10px;
}

.searchPage_resultImage{
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-right: 10px;
}